* {
  margin: 0;
  box-sizing: border-box;
}

.main-div {
  font-family: sans-serif;
  width: 100%;
  height: 100vh;
  background-color: #eef1fb;
  text-align: center;
  color: #fff;
}

.h1 {
  padding: 30px;
  color: #364a76;
}

.box {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.box-1,
.box-2,
.box-3,
.box-4,
.box-5,
.box-6 {
  width: 30%;
  height: 160px;
  border-radius: 10px;
  margin: 10px auto;
}


.box-1,
.box-3,
.box-5 {
  background-color: #2c4676;
}

.box-2,
.box-4,
.box-6 {
  background: #8ebfc3;
}

span {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

button {
  padding: 8px 20px;
  background: #2c4676;
  color: #fff;
  border: 1px solid transparent;
  margin: 20px 10px;
}